import { ITodoCategory } from "../../dtos/ITodoCategory";

interface ICategoryEditProps {
    handleEditChange: (target: HTMLInputElement) => void;
    values: ITodoCategory;
    editCategory: (target: EventTarget) => void;
}

const CategoryEdit = (props: ICategoryEditProps) => {
    return (
        <>
            <form>
                <div className="flex flex-col space-y-4 p-4 border-2 border-white rounded-md  text-white dark:bg-transparent">
                    <div className="flex flex-col font-semibold">
                        <label htmlFor="categoryName">Category Name</label>
                        <input id="categoryName"
                            type="text"
                            className="p-1 bg-white text-black"
                            value={props.values.categoryName}
                            onChange={(e) => props.handleEditChange(e.target)}
                        >
                        </input>
                    </div>
                    <div className="flex flex-col font-semibold">
                        <label htmlFor="categorySort">Priority</label>
                        <input id="categorySort"
                            type="number"
                            className="p-1 bg-white text-black"
                            value={props.values.categorySort}
                            onChange={(e) => props.handleEditChange(e.target)}
                        >
                        </input>
                    </div>
                    <div className="flex flex-row justify-center">
                        <button
                            className="p-2 bg-yellow-200 w-full font-bold text-black"
                            onClick={(e) => { props.editCategory(e.target); e.preventDefault() }}>
                            Edit
                        </button>
                    </div>
                </div>
            </form>
        </>
    )
}

export default CategoryEdit;