import React, { useContext, useEffect, useState } from "react";
import { Redirect } from "react-router-dom";
import Loader from "../../components/Loader";
import { AppContext } from "../../context/AppContext";
import { ITodoCategory } from "../../dtos/ITodoCategory";
import { BaseService } from "../../services/base-service";
import { EPageStatus } from "../../types/EPageStatus";
import { IPageStatus } from "../../types/IPageStatus";
import CategoryAdd from "./CategoryAdd";
import CategoryCards from "./CategoryCards";
import CategoryEdit from "./CategoryEdit";


const initialFormValues: ITodoCategory = {
    categoryName: "",
    categorySort: 0,
}

export const initialSelectedCategory: ITodoCategory = {
    categoryName: "",
    categorySort: 0
}

const CategoriesIndex = () => {
    const [categories, setCategories] = useState([] as ITodoCategory[]);
    const [pageStatus, setPageStatus] = useState({ pageStatus: EPageStatus.Loading, statusCode: -1 } as IPageStatus);
    const [formValues, setFormValues] = useState(initialFormValues)
    const [selectedCategory, setSelectedCategory] = useState(initialSelectedCategory);

    const appState = useContext(AppContext);

    console.log(selectedCategory);

    const loadData = async () => {
        const result = await BaseService.getAll<ITodoCategory>("/TodoCategories", appState.jwt ?? "");

        if (result.ok && result.data) {
            setCategories(result.data);
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
        }
        else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages });
        }
    }

    const addCategory = async () => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 });

        const result = await BaseService.post<ITodoCategory>("/TodoCategories", formValues, appState.jwt ?? "");
        if (result.ok && result.data) {
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
            loadData();
            setFormValues(initialFormValues);
        }
        else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages });
        }
    }

    const removeCategory = async (category: ITodoCategory) => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 });
        const result = await BaseService.delete<ITodoCategory>("/TodoCategories", category.id ?? "", appState.jwt ?? "");
        if (result.ok) {
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
            loadData();
            setFormValues(initialFormValues);
            setSelectedCategory(initialSelectedCategory);
        }
        else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages });
        }
    }

    const editCategory = async () => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 });
        const result = await BaseService.put<ITodoCategory>("/TodoCategories", selectedCategory, selectedCategory.id ?? "", appState.jwt ?? "");
        if (result.ok) {
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
            loadData();
            setFormValues(initialFormValues);
            setSelectedCategory(initialSelectedCategory);
        }
        else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages });
        }

    }

    const handleChange = (target: HTMLInputElement) => {
        switch (target.id) {
            case "categoryName":
                setFormValues({ ...formValues, categoryName: target.value })
                return;
            case "categorySort":
                setFormValues({ ...formValues, categorySort: Number.parseInt(target.value) })
                return;
            default:
                return;
        }
    }

    const handleEditChange = (target: HTMLInputElement) => {
        switch (target.id) {
            case "categoryName":
                setSelectedCategory({ ...selectedCategory, categoryName: target.value })
                return;
            case "categorySort":
                setSelectedCategory({ ...selectedCategory, categorySort: Number.parseInt(target.value) })
                return;
            default:
                return;
        }
    }

    useEffect(() => {
        loadData()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <>
            {
                appState.jwt !== null
                    ?
                    <>
                        <Loader pageStatus={pageStatus} />
                        <div className="flex flex-col border-b border-t border-white p-4 space-y-4">
                            {
                                selectedCategory.id
                                    ?
                                    <CategoryEdit
                                        handleEditChange={handleEditChange}
                                        values={selectedCategory}
                                        editCategory={editCategory}
                                    />
                                    :
                                    <CategoryAdd
                                        handleChange={handleChange}
                                        values={formValues}
                                        addCategory={addCategory}
                                    />
                            }
                            <CategoryCards
                                removeCategory={removeCategory}
                                categories={categories}
                                selectCategory={setSelectedCategory}
                                selectedCategory={selectedCategory}
                            />
                        </div>
                    </>
                    :
                    <Redirect to="/" />
            }
        </>
    );
}



export default CategoriesIndex;