import { ITodoPriority } from "../../dtos/ITodoPriority";

interface IPriorityEditProps {
    handleEditChange: (target: HTMLInputElement) => void;
    values: ITodoPriority;
    editPriority: (target: EventTarget) => void;
}

const PriorityEdit = (props: IPriorityEditProps) => {
    return (
        <>
            <form>
                <div className="flex flex-col space-y-4 p-4 border-2 border-white rounded-md  text-white dark:bg-transparent">
                    <div className="flex flex-col font-semibold">
                        <label htmlFor="priorityName">Priority Name</label>
                        <input id="priorityName"
                            type="text"
                            className="p-1 bg-white text-black"
                            value={props.values.priorityName}
                            onChange={(e) => props.handleEditChange(e.target)}
                        >
                        </input>
                    </div>
                    <div className="flex flex-col font-semibold">
                        <label htmlFor="prioritySort">Priority level</label>
                        <input id="prioritySort"
                            type="number"
                            className="p-1 bg-white text-black"
                            value={props.values.prioritySort}
                            onChange={(e) => props.handleEditChange(e.target)}
                        >
                        </input>
                    </div>
                    <div className="flex flex-row justify-center">
                        <button
                            className="p-2 bg-yellow-200 w-full font-bold text-black"
                            onClick={(e) => { props.editPriority(e.target); e.preventDefault() }}>
                            Edit
                        </button>
                    </div>
                </div>
            </form>
        </>
    )
}

export default PriorityEdit;