import { ITodoPriority } from "../../dtos/ITodoPriority";
import { initialSelectedPriority } from "./PrioritiesIndex";

export interface IPriorityCardProps {
    priorities: ITodoPriority[];
    removePriority: (priority: ITodoPriority) => void;
    selectPriority: (priority: ITodoPriority) => void;
    selectedPriority: ITodoPriority;
}

const PriorityCards = (props: IPriorityCardProps) => {

    const PriorityCard = (priority: ITodoPriority) => {
        return (
            <div className="flex w-full h-14 dark:border-white border rounded-md">
                {
                    props.selectedPriority.id === priority.id
                        ?
                        <>
                            <div
                                onClick={() => props.selectedPriority.id === priority.id ? props.selectPriority(initialSelectedPriority) : props.selectPriority(priority)}
                                className="flex w-4/5 flex-row space-x-4 p-4 rounded-l-md bg-yellow-100 text-blue-500 dark:text-white dark:bg-transparent cursor-pointer">
                                <div className="font-semibold w-1/2">
                                    {priority.priorityName}
                                </div>
                                <div className="w-1/2">
                                    <label>Priority Level: </label>
                                    {priority.prioritySort}
                                </div>
                            </div>
                            <div className="flex w-1/5 flex-row ">
                                <button
                                    className=" w-full h-full p-4 bg-red-500 rounded-r-md text-white"
                                    onClick={() => props.removePriority(priority)}>
                                    Remove
                            </button>
                            </div>
                        </>
                        :
                        <>
                            <div
                                onClick={() => props.selectedPriority.id === priority.id ? props.selectPriority(initialSelectedPriority) : props.selectPriority(priority)}
                                className="flex w-full flex-row space-x-4 p-4 rounded-md bg-white text-blue-500 dark:text-white dark:bg-transparent cursor-pointer hover:bg-yellow-200 dark:hover:bg-gray-400">
                                <div className="font-semibold w-1/2">
                                    {priority.priorityName}
                                </div>
                                <div className="w-1/2">
                                    <label>Priority: </label>
                                    {priority.prioritySort}
                                </div>
                                <div>
                                    ↙
                                </div>
                            </div>
                        </>

                }
            </div>
        )
    }

    return (
        <div className="space-y-2">
            {
                props.priorities
                    .sort((a, b) => a.prioritySort > b.prioritySort ? -1 : 1)
                    .map(priority => {
                        return <PriorityCard {...priority} key={priority.id} />;
                    })
            }
        </div>)



}


export default PriorityCards;
