import React, { useContext, useEffect, useState } from "react";
import { Redirect } from "react-router-dom";
import Loader from "../../components/Loader";
import { AppContext } from "../../context/AppContext";
import { ITodoPriority } from "../../dtos/ITodoPriority";
import { BaseService } from "../../services/base-service";
import { EPageStatus } from "../../types/EPageStatus";
import { IPageStatus } from "../../types/IPageStatus";
import PriorityAdd from "./PriorityAdd";
import PriorityCards from "./PriorityCards";
import PriorityEdit from "./PriorityEdit";


const initialFormValues: ITodoPriority = {
    priorityName: "",
    prioritySort: 0
}

export const initialSelectedPriority: ITodoPriority = {
    priorityName: "",
    prioritySort: 0
}

const PrioritiesIndex = () => {
    const [priorities, setPriorities] = useState([] as ITodoPriority[]);
    const [pageStatus, setPageStatus] = useState({ pageStatus: EPageStatus.Loading, statusCode: -1 } as IPageStatus);
    const [formValues, setFormValues] = useState(initialFormValues)
    const [selectedPriority, setSelectedPriority] = useState(initialSelectedPriority);

    const appState = useContext(AppContext);

    console.log(selectedPriority);

    const loadData = async () => {
        const result = await BaseService.getAll<ITodoPriority>("/TodoPriorities", appState.jwt ?? "");

        if (result.ok && result.data) {
            setPriorities(result.data);
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
        }
        else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages });
        }
    }

    const addPriority = async () => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 });

        const result = await BaseService.post<ITodoPriority>("/TodoPriorities", formValues, appState.jwt ?? "");
        if (result.ok && result.data) {
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
            loadData();
            setFormValues(initialFormValues);
        }
        else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages });
        }
    }

    const removePriority = async (category: ITodoPriority) => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 });
        const result = await BaseService.delete<ITodoPriority>("/TodoPriorities", category.id ?? "", appState.jwt ?? "");
        if (result.ok) {
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
            loadData();
            setFormValues(initialFormValues);
            setSelectedPriority(initialSelectedPriority);
        }
        else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages });
        }
    }

    const editPriority = async () => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 });
        const result = await BaseService.put<ITodoPriority>("/TodoPriorities", selectedPriority, selectedPriority.id ?? "", appState.jwt ?? "");
        if (result.ok) {
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
            loadData();
            setFormValues(initialFormValues);
            setSelectedPriority(initialSelectedPriority);
        }
        else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages });
        }

    }

    const handleChange = (target: HTMLInputElement) => {
        switch (target.id) {
            case "priorityName":
                setFormValues({ ...formValues, priorityName: target.value })
                return;
            case "prioritySort":
                setFormValues({ ...formValues, prioritySort: Number.parseInt(target.value) })
                return;
            default:
                return;
        }
    }

    const handleEditChange = (target: HTMLInputElement) => {
        switch (target.id) {
            case "priorityName":
                setSelectedPriority({ ...selectedPriority, priorityName: target.value })
                return;
            case "prioritySort":
                setSelectedPriority({ ...selectedPriority, prioritySort: Number.parseInt(target.value) })
                return;
            default:
                return;
        }
    }

    useEffect(() => {
        loadData()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <>
            {
                appState.jwt !== null
                    ?
                    <>
                        <Loader pageStatus={pageStatus} />
                        <div className="flex flex-col border-b border-t border-white p-4 space-y-4">
                            {
                                selectedPriority.id
                                    ?
                                    <PriorityEdit
                                        handleEditChange={handleEditChange}
                                        values={selectedPriority}
                                        editPriority={editPriority}
                                    />
                                    :
                                    <PriorityAdd
                                        handleChange={handleChange}
                                        values={formValues}
                                        addPriority={addPriority}
                                    />
                            }
                            <PriorityCards
                                removePriority={removePriority}
                                priorities={priorities}
                                selectPriority={setSelectedPriority}
                                selectedPriority={selectedPriority}
                            />
                        </div>
                    </>
                    :
                    <Redirect to="/" />
            }
        </>
    );
}



export default PrioritiesIndex;