import { ITodoCategory } from "../../dtos/ITodoCategory";
import { initialSelectedCategory } from "./CategoriesIndex";

export interface ICategoryCardProps {
    categories: ITodoCategory[];
    removeCategory: (category: ITodoCategory) => void;
    selectCategory: (category: ITodoCategory) => void;
    selectedCategory: ITodoCategory;
}

const CategoryCards = (props: ICategoryCardProps) => {

    const CategoryCard = (category: ITodoCategory) => {
        return (
            <div className="flex w-full h-14 dark:border-white border rounded-md">
                {
                    props.selectedCategory.id === category.id
                        ?
                        <>
                            <div
                                onClick={() => props.selectedCategory.id === category.id ? props.selectCategory(initialSelectedCategory) : props.selectCategory(category)}
                                className="flex w-4/5 flex-row space-x-4 p-4 rounded-l-md bg-yellow-100 text-blue-500 dark:text-white dark:bg-transparent cursor-pointer">
                                <div className="font-semibold w-1/2">
                                    {category.categoryName}
                                </div>
                                <div className="w-1/2">
                                    <label>Priority: </label>
                                    {category.categorySort}
                                </div>
                            </div>
                            <div className="flex w-1/5 flex-row ">
                                <button
                                    className=" w-full h-full p-4 bg-red-500 rounded-r-md text-white"
                                    onClick={() => props.removeCategory(category)}>
                                    Remove
                            </button>
                            </div>
                        </>
                        :
                        <>
                            <div
                                onClick={() => props.selectedCategory.id === category.id ? props.selectCategory(initialSelectedCategory) : props.selectCategory(category)}
                                className="flex w-full flex-row space-x-4 p-4 rounded-md bg-white text-blue-500 dark:text-white dark:bg-transparent cursor-pointer hover:bg-yellow-200 dark:hover:bg-gray-400">
                                <div className="font-semibold w-1/2">
                                    {category.categoryName}
                                </div>
                                <div className="w-1/2">
                                    <label>Priority: </label>
                                    {category.categorySort}
                                </div>
                                <div>
                                    ↙
                                </div>
                            </div>
                        </>

                }
            </div>
        )
    }

    return (
        <div className="space-y-2">
            {
                props.categories
                    .sort((a, b) => a.categorySort > b.categorySort ? -1 : 1)
                    .map(category => {
                        return <CategoryCard {...category} key={category.id} />;
                    })
            }
        </div>)



}


export default CategoryCards;
